import React from "react";

import '../css/home.css'

interface WorkInfoProps {
    professional: string;
    medical: string;
    health: string;
}
const WorkInfo: React.FC<WorkInfoProps> = (props) => {
    return (
        <div>
            <div className="info-container">
                <div className="svg-text">
                    <svg width="128" height="128" viewBox="0 0 128 128" fill="none" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
                        <rect width="128" height="128" fill="url(#pattern0_12_69)" />
                        <defs>
                            <pattern id="pattern0_12_69" patternContentUnits="objectBoundingBox" width="1" height="1">
                                <use xlinkHref="#image0_12_69" transform="scale(0.0078125)" />
                            </pattern>
                            <image id="image0_12_69" width="128" height="128" xlinkHref="data:image/png;base64," />
                        </defs>
                    </svg>
                    <p className="text-svg">{props.professional}</p>
                </div>
                <div className="svg-text">
                    <svg width="128" height="128" viewBox="0 0 128 128" fill="none" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
                        <rect width="128" height="128" fill="url(#pattern0_12_73)" />
                        <defs>
                            <pattern id="pattern0_12_73" patternContentUnits="objectBoundingBox" width="1" height="1">
                                <use xlinkHref="#image0_12_73" transform="scale(0.0078125)" />
                            </pattern>
                            <image id="image0_12_73" width="128" height="128" xlinkHref="data:image/png;base64," />
                        </defs>
                    </svg>
                    <p className="text-svg">{props.medical}</p>
                </div>
                <div className="svg-text">
                    <svg width="128" height="128" viewBox="0 0 128 128" fill="none" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
                        <rect width="128" height="128" fill="url(#pattern0_12_71)" />
                        <defs>
                            <pattern id="pattern0_12_71" patternContentUnits="objectBoundingBox" width="1" height="1">
                                <use xlinkHref="#image0_12_71" transform="scale(0.0078125)" />
                            </pattern>
                            <image id="image0_12_71" width="128" height="128" xlinkHref="data:image/png;base64," />
                        </defs>
                    </svg>
                    <p className="text-svg">{props.health}</p>
                </div>
            </div>
        </div>
    )
}

const WorkInfoContainer: React.FC = () => {
    const professional = 'Каждый из нас проходил профессиональное обучение по тату мастерству ';
    const medical = 'У всех сотрудников имеется медицинское образование для вашей безопасности';
    const health = 'С нами вы можете не беспокоиться о своем здоровье! Мы строго следим за санитарией!';

    return (
        <WorkInfo
        professional={professional}
        medical={medical}
        health={health}
        />
    )
}

export default WorkInfoContainer;