import React from 'react';
import '../css/home.css'

interface TattooInfoProps {
  titleTattoo: string;
  textTattoo: string;
  titleOverlap: string;
  textOverlap: string;
  titleTemporary: string;
  textTemporary: string;
  titleEducation: string;
  textEducation: string;
}

const TattooInfo: React.FC<TattooInfoProps> = (props) => {
  return (
    <div>
      <h2>
        Что мы умеем?
      </h2>
      <div className='rectangles-container'>

        <div className='rectangle'>
          <svg width="90" height="94" viewBox="0 0 90 94" fill="none" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
            <rect y="0.476715" width="90" height="92.5521" fill="url(#pattern0_7_10)" />
            <defs>
              <pattern id="pattern0_7_10" patternContentUnits="objectBoundingBox" width="1" height="1">
                <use xlinkHref="#image0_7_10" transform="matrix(0.00803404 0 0 0.0078125 -0.0141784 0)" />
              </pattern>
              <image id="image0_7_10" width="128" height="128" xlinkHref="data:image/png;base64," />
            </defs>
          </svg>
          <h2 className='rect-h2'>{props.titleTattoo}</h2>
          <p className='rect-p'>{props.textTattoo}</p>
        </div>

        <div className='rectangle'>
          <svg width="90" height="94" viewBox="0 0 90 94" fill="none" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
            <rect y="0.476715" width="90" height="92.5521" fill="url(#pattern0_7_11)" />
            <defs>
              <pattern id="pattern0_7_11" patternContentUnits="objectBoundingBox" width="1" height="1">
                <use xlinkHref="#image0_7_11" transform="matrix(0.00803404 0 0 0.0078125 -0.0141784 0)" />
              </pattern>
              <image id="image0_7_11" width="128" height="128" xlinkHref="data:image/png;base64," />
            </defs>
          </svg>
          <h2 className='rect-h2'>{props.titleOverlap}</h2>
          <p className='rect-p'>{props.textOverlap}</p>
        </div>

        <div className='rectangle'>
          <svg width="89" height="89" viewBox="0 0 89 89" fill="none" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
            <rect width="89" height="89" fill="url(#pattern0_7_14)" />
            <defs>
              <pattern id="pattern0_7_14" patternContentUnits="objectBoundingBox" width="1" height="1">
                <use xlinkHref="#image0_7_14" transform="scale(0.0078125)" />
              </pattern>
              <image id="image0_7_14" width="128" height="128" xlinkHref="data:image/png;base64," />
            </defs>
          </svg>
          <h2 className='rect-h2'>{props.titleTemporary}</h2>
          <p className='rect-p'>{props.textTemporary}</p>
        </div>

        <div className='rectangle'>
          <svg width="91" height="91" viewBox="0 0 91 91" fill="none" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
            <rect width="91" height="91" fill="url(#pattern0_7_16)" />
            <defs>
              <pattern id="pattern0_7_16" patternContentUnits="objectBoundingBox" width="1" height="1">
                <use xlinkHref="#image0_7_16" transform="scale(0.0078125)" />
              </pattern>
              <image id="image0_7_16" width="128" height="128" xlinkHref="data:image/png;base64," />
            </defs>
          </svg>
          <h2 className='rect-h2'>{props.titleEducation}</h2>
          <p className='rect-p'>{props.textEducation}</p>
        </div>

      </div>
    </div>
  );
}

const TattooInfoContainer: React.FC = () => {
  const titleTattoo = 'Tattoo';
  const textTattoo = 'Татуировки любого вида и художественного стиля. От привитизма до гиперреализма. Бесплатная индивидуальная консультация, подбор эскизов';
  const titleOverlap = 'Overlap / cover up';
  const textOverlap = 'Перекрытие ожогов и шрамов, скрытие растяжек и дефектов кожи. Исправление некачественных тату “дворовых” мастеров. Раскрашивание и обновление старых татуировок';
  const titleTemporary = 'temporary tattoo';
  const textTemporary = 'Мехенди - роспись по телу (хной). Древний способ украсить тело без прокола кожи. Это временная татуировка на 2-3 недели. Быстро, стильно, не дорого';
  const titleEducation = 'education';
  const textEducation = 'Индивидуальные занятия в нашей студии. Теоретическая часть + практические уроки на искусственной коже + на моделях. Предоставляем все необходимое оборудование и материалы';

  return (
    <TattooInfo
      titleTattoo={titleTattoo}
      textTattoo={textTattoo}
      titleOverlap={titleOverlap}
      textOverlap={textOverlap}
      titleTemporary={titleTemporary}
      textTemporary={textTemporary}
      titleEducation={titleEducation}
      textEducation={textEducation}
    />
  );
}

export default TattooInfoContainer;
